.product-container {
  width: 17em;
  padding: 2rem;
  background-color: white;
  border-radius: 5px;
  margin-bottom: 1rem;
  margin-right: 1rem;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.25);
  transition: none;
}

@media (min-width: 900px) {
  .product-container {
    transition: 0.3s all linear;
  }
  .product-container:hover {
    transform: scale(1.05);
  }
}

.image-resize {
  width: 17em;
}

.meta-container .product_name {
  font-size: 16px;
  color: #393c45;
}
.meta-container p {
  font-size: 12px;
  color: #b1b1b3;
  padding: 2px 0 2px 0;
  height: 2.5rem;
}
.meta-container .product_price {
  float: right;
  color: #48cfad;
  font-size: 13px;
  font-weight: 400;
}
.meta-container .product_gender {
  font-size: 11px;
  color: #4d4d50;
}

.meta-container .extra-details {
  display: flex;
  justify-content: space-between;
}

.addToCart {
  cursor: pointer;
  border: 1px solid #115dd8;
  padding: 10px 20px;
  border-radius: 200px;
  color: #fff;
  background: #115dd8;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  transition: 200ms;
  float: right;
}

.addToCart :hover {
  background: #fff;
  color: #115dd8;
}

.button-container {
  margin-top: 1rem;
}
